.Navbar{
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 2rem;
    padding: 1rem 2rem;
    /* position: fixed;
    top: 0;
    left: 0;
    right: 0; */
    /* background-color: #f4f4f4; */
    z-index: 100;
}

.Navbar .logo{
    height: 2rem;
    width: 2rem;
}

.Navbar .nav-btn{
    display: flex;
    align-items: center;
    gap: 1rem;
    height: 100%;
}

.Navbar .nav-btn svg {
    padding: 0 1.5rem;
    stroke: #e3e3e3;
}

.Navbar svg{
    height: 1.5rem;
    width: 1.5rem;
    padding: 0 0.5rem;
}

.Navbar .profile-img{
    aspect-ratio: 1/1;
    height: 90%;
    border-radius: 100%;
}